.Stat {
    width: 23rem;
    padding: 2rem 0.5rem;
    border-radius: 50%;

    margin: 1rem;
    text-align: center;

    &__icon {
        width: 100%;
        font-size: 4rem;
        margin-bottom: 1rem;
    }

    &__number {
        width: 100%;
        text-align: center;
        font-size: 3rem;
        font-weight: 600;
    }

    &__name {
        width: 100%;
        text-align: center;
        font-size: 2rem;
        margin-top: 0.5rem;
    }
}
