.ScrollView {
    position: relative;
    pointer-events: none;
    overflow: hidden;
    display: flex;
    flex-direction: column;

    .ScrollView__Rod {
        position: absolute;
        top: 0;
        bottom: 0;

        &.ScrollView__Rod--left {
            left: var(--page-margin, 0);
        }

        &.ScrollView__Rod--right {
            right: var(--page-margin, 0);
        }

        &__vertical {
            background-color: white;
            width: 1px;
            height: 100%;
        }

        &__cross {
            position: absolute;
            top: 7px;
            width: 15px;
            margin-left: -7px;
            height: 1px;
            background-color: white;
        }
    }

    .ScrollView__handle-container {
        position: absolute;
        right: var(--page-margin, 0);
        margin-top: 7px;
        pointer-events: initial;
    }

    .ScrollView__handle {
        position: absolute;
        left: -8px;
        width: 15px;
        height: 100px;
        background-color: white;
        pointer-events: initial;

        &:hover {
            cursor: pointer;
        }
    }

    .ScrollView__contents {
        overflow-y: scroll;
        pointer-events: initial;

        /* Hide scrollbar for Chrome, Safari and Opera */
        &::-webkit-scrollbar {
            display: none;
        }

        /* Hide scrollbar for IE, Edge and Firefox */
        -ms-overflow-style: none; /* IE and Edge */
        scrollbar-width: none; /* Firefox */
    }
}
