.TeamCard {
    * {
        box-sizing: border-box;
    }

    $size: 1;
    $protrusion: 20%;
    $gradient: linear-gradient(135deg, #21d4fd, #b721ff); // linear-gradient(135deg, #F84949 , #AC2A92);

    width: 100%;
    max-width: 70rem;
    background-color: white;
    margin-top: 7rem;
    transition-duration: 0.5s;
    box-shadow: 2px 3px 1rem rgba(0, 0, 0, 0.63);
    pointer-events: auto;
    display: flex;
    min-height: 16rem;
    position: relative;

    &:hover {
        transform: scale(1.05) translateY(-1rem);
        box-shadow: 0px 5px 2rem rgba(0, 0, 0, 0.753);
    }

    &__img-block {
        width: 60%;
        height: 100%;
        position: relative;
        background: $gradient;
        background: none !important;
    }

    &__img {
        position: absolute;
        width: 100% + $protrusion;
        height: 100% + $protrusion;
        object-fit: cover;
        object-position: 40% 0%;
        top: -$protrusion;
        // left: -$protrusion/2;
        right: 0;
    }

    &__img-background {
        width: 100%;
        height: 100%;
        object-fit: scale-down;
    }

    &__heading {
        color: black;
        display: block;
        width: 100%;
        font-size: 2rem * $size;
        text-transform: uppercase;
        text-align: center;
        z-index: 50;
        padding: 1rem;
        margin: 0rem;
    }

    &__content {
        position: relative;
        background-color: #ffffff;
        background: linear-gradient(95deg, #ffffff00 5%, #ffffff 6%, #ffffff 94%, #ffffff00 95%);
        width: 100%;
        height: 100%;
        padding: 1rem 4rem;
        color: #000000;
        display: flex;
        justify-content: center;
        align-content: center;
        flex-wrap: wrap;
        z-index: 10;
    }

    &__description {
        display: block;
        margin: 0rem;
        width: 100%;
        font-size: 1.2rem * $size;
        opacity: 0.7;
        text-align: center;
    }

    &__stats-container {
        width: 50%;
        height: 100%;
        background: $gradient;
        display: flex;
        flex-direction: column;
        background: none !important;
    }

    &__stat {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-content: center;
        flex-wrap: wrap;
        padding: 2px;
        line-height: 2rem * $size;
        color: white;

        &:not(:first-child) {
            border-top: 1px solid #ffffff55;
        }
    }

    &__value {
        width: 100%;
        font-size: 1.5rem * $size;
        text-align: center;

        &--big {
            font-size: 2rem * $size;
        }
    }
    
    &__name {
        width: 100%;
        font-size: 1rem * $size;
        opacity: 0.7;
        text-align: center;
        //text-transform: uppercase;
    }
}
