.SideStoryPanel {
    display: flex;
    align-items: center;
    justify-content: flex-end;

    pointer-events: none;
    transition: 0.2s ease-out opacity;
    
    .SideStoryPanel--panel {
        box-sizing: border-box;
        width: 50%;
        padding-right: var(--page-margin, 0);
        opacity: 0;
        transition: 0.2s ease-out opacity;

        .SideStoryPanel--title {
            display: block;
            color: white;
            font-size: 3rem;
            margin: 0;
            margin-bottom: 3rem;
        }

        .SideStoryPanel--description {
            margin-right: 10rem;
            color: white;
            font-size: 1.5rem;
            max-width: 100rem;
            text-align: justify;
        }
    }

    &.visible {
        .SideStoryPanel--panel {
            opacity: 1;
        }
    }
}