@import "home/timeline/Timeline.scss";

html {
    font-size: 75%;
    box-sizing: border-box;
}

@media only screen and (max-width: 1000px) {
    html {
        font-size: 60%;
    }
}

body {
    background-color: black;
}

.App {
    min-height: 100vh;

    // The default color scheme
    --background-color: #19284d;
    --background-color-light: #203662;
    --background-color-lighter: #2c5384;

    // Setting up the NZN color scheme
    &.nzn {
        --background-color: #6b0000;
        --background-color-light: #ce3c10;
        --background-color-lighter: #ffb62f;
    }

    // Setting up the Contact color scheme
    &.contact {
        --background-color: #000000;
        --background-color-light: #222222;
        --background-color-lighter: #313131;
    }

    .App__main {
        position: relative;
        min-height: 100vh;
        overflow-x: hidden;
        overflow-y: hidden;

        &.App__main--page-switch-left {
            --page-switch-shove: -50px;
        }

        .App__page-content {
            width: 100vw;

            &.router-fade-enter {
                position: fixed;
                top: 0;
                left: 0;
                right: 0;
                opacity: 0.0;
                transform: translateX(var(--page-switch-shove, 50px));
            }

            &.router-fade-enter.router-fade-enter-active {
                transform: translateX(0px);
                opacity: 1;
                transition: opacity 150ms ease-in 150ms, transform 150ms ease-out 150ms;
            }

            &.router-fade-exit {
                opacity: 1;
            }

            &.router-fade-exit.router-fade-exit-active {
                opacity: 0.01;
                transition: opacity 150ms ease-in, transform 150ms ease-out;
            }
        }
    }


    &:after {
        animation: grain 8s steps(10) infinite;
        background-image: url("../assets/img/img-noise-512x512.png");
        content: "";
        height: 300%;
        left: -50%;
        opacity: 0.3;
        position: fixed;
        z-index: 10000;
        top: -100%;
        width: 300%;
        mix-blend-mode: darken;
        pointer-events: none;
    }

    @keyframes grain {

        0%,
        100% {
            transform: translate(0, 0)
        }

        10% {
            transform: translate(-5%, -10%)
        }

        20% {
            transform: translate(-15%, 5%)
        }

        30% {
            transform: translate(7%, -25%)
        }

        40% {
            transform: translate(-5%, 25%)
        }

        50% {
            transform: translate(-15%, 10%)
        }

        60% {
            transform: translate(15%, 0%)
        }

        70% {
            transform: translate(0%, 15%)
        }

        80% {
            transform: translate(3%, 35%)
        }

        90% {
            transform: translate(-10%, 10%)
        }
    }
}
