        sans-serif; */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

#threejs-container {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
}
.Timeline__label{color:white;font-weight:600;width:50rem;text-align:center;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);z-index:0}.Timeline__label-date{font-size:8rem;margin:1rem;pointer-events:none}.Timeline__label-header{font-size:4rem;margin:0.5rem}.Timeline__label-description{font-size:2.4rem;margin:0.5rem}body>div:nth-child(8){z-index:-1}html{font-size:75%;box-sizing:border-box}@media only screen and (max-width: 1000px){html{font-size:60%}}body{background-color:black}.App{min-height:100vh;--background-color: #19284d;--background-color-light: #203662;--background-color-lighter: #2c5384}.App.nzn{--background-color: #6b0000;--background-color-light: #ce3c10;--background-color-lighter: #ffb62f}.App.contact{--background-color: #000000;--background-color-light: #222222;--background-color-lighter: #313131}.App .App__main{position:relative;min-height:100vh;overflow-x:hidden;overflow-y:hidden}.App .App__main.App__main--page-switch-left{--page-switch-shove: -50px}.App .App__main .App__page-content{width:100vw}.App .App__main .App__page-content.router-fade-enter{position:fixed;top:0;left:0;right:0;opacity:0.0;-webkit-transform:translateX(50px);transform:translateX(50px);-webkit-transform:translateX(var(--page-switch-shove, 50px));transform:translateX(var(--page-switch-shove, 50px))}.App .App__main .App__page-content.router-fade-enter.router-fade-enter-active{-webkit-transform:translateX(0px);transform:translateX(0px);opacity:1;transition:opacity 150ms ease-in 150ms, -webkit-transform 150ms ease-out 150ms;transition:opacity 150ms ease-in 150ms, transform 150ms ease-out 150ms;transition:opacity 150ms ease-in 150ms, transform 150ms ease-out 150ms, -webkit-transform 150ms ease-out 150ms}.App .App__main .App__page-content.router-fade-exit{opacity:1}.App .App__main .App__page-content.router-fade-exit.router-fade-exit-active{opacity:0.01;transition:opacity 150ms ease-in, -webkit-transform 150ms ease-out;transition:opacity 150ms ease-in, transform 150ms ease-out;transition:opacity 150ms ease-in, transform 150ms ease-out, -webkit-transform 150ms ease-out}.App:after{-webkit-animation:grain 8s steps(10) infinite;animation:grain 8s steps(10) infinite;background-image:url(/agh-solar-plane/static/media/img-noise-512x512.fc205488.png);content:"";height:300%;left:-50%;opacity:0.3;position:fixed;z-index:10000;top:-100%;width:300%;mix-blend-mode:darken;pointer-events:none}@-webkit-keyframes grain{0%,100%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}10%{-webkit-transform:translate(-5%, -10%);transform:translate(-5%, -10%)}20%{-webkit-transform:translate(-15%, 5%);transform:translate(-15%, 5%)}30%{-webkit-transform:translate(7%, -25%);transform:translate(7%, -25%)}40%{-webkit-transform:translate(-5%, 25%);transform:translate(-5%, 25%)}50%{-webkit-transform:translate(-15%, 10%);transform:translate(-15%, 10%)}60%{-webkit-transform:translate(15%, 0%);transform:translate(15%, 0%)}70%{-webkit-transform:translate(0%, 15%);transform:translate(0%, 15%)}80%{-webkit-transform:translate(3%, 35%);transform:translate(3%, 35%)}90%{-webkit-transform:translate(-10%, 10%);transform:translate(-10%, 10%)}}@keyframes grain{0%,100%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}10%{-webkit-transform:translate(-5%, -10%);transform:translate(-5%, -10%)}20%{-webkit-transform:translate(-15%, 5%);transform:translate(-15%, 5%)}30%{-webkit-transform:translate(7%, -25%);transform:translate(7%, -25%)}40%{-webkit-transform:translate(-5%, 25%);transform:translate(-5%, 25%)}50%{-webkit-transform:translate(-15%, 10%);transform:translate(-15%, 10%)}60%{-webkit-transform:translate(15%, 0%);transform:translate(15%, 0%)}70%{-webkit-transform:translate(0%, 15%);transform:translate(0%, 15%)}80%{-webkit-transform:translate(3%, 35%);transform:translate(3%, 35%)}90%{-webkit-transform:translate(-10%, 10%);transform:translate(-10%, 10%)}}

.HeaderLogo{height:6rem}

.Header{z-index:1;position:fixed;top:40px;display:flex;box-sizing:border-box;width:100%;justify-content:flex-start;align-items:center;margin:0 0px;margin:0 var(--page-margin, 0px)}.Header .actions{margin:3rem;display:flex;justify-content:space-around;align-items:center;padding:0 2rem;border-radius:0.1rem}.Header .actions a{position:relative;padding:1rem 3rem;color:white;border:0;font-size:1rem;text-decoration:none;transition:0.2s ease-out color, 0.2s ease-out background-color}.Header .actions a::after{content:"";position:absolute;bottom:25%;left:50%;-webkit-transform:translateX(-50%) translateY(2px) scaleX(0);transform:translateX(-50%) translateY(2px) scaleX(0);width:calc(100% - 6rem);height:1px;background:white;transition:0.1s ease-in-out background-color, 0.1s ease-out transform}.Header .actions a.active{background-color:white;color:black}.Header .actions a:not(.active):hover::after{-webkit-transform:translateX(-50%) translateY(2px) scaleX(1);transform:translateX(-50%) translateY(2px) scaleX(1)}

.Banner{position:relative;width:100%;height:100vh}.Banner__overlay{width:100%;height:100vh;display:flex;align-items:center}.Banner__overlay-text{position:absolute;box-sizing:border-box;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);color:#ffffff;width:200rem;max-width:80%;padding:3rem;font-size:4.5rem;line-height:1.5;font-weight:700;text-align:left}.Banner__action{width:100%;height:20vh;position:absolute;left:0;bottom:0;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:2rem;font-weight:bold;color:white;overflow:hidden;text-transform:uppercase;opacity:.6}.Banner__social-media{position:absolute;bottom:10rem;z-index:100;width:100%;display:flex;flex-wrap:wrap;justify-content:center}.Banner__social-media-pair{position:relative;width:75rem;height:10rem;display:flex;justify-content:space-around}.Banner__social-media-link{width:20rem;height:10rem;position:relative;-webkit-transform:scale(1);transform:scale(1);transition-duration:.5s}.Banner__social-media-link:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.Banner__social-media-img{position:absolute;width:100%;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);-webkit-filter:blur(0px) grayscale(1) brightness(0.5) invert(1);filter:blur(0px) grayscale(1) brightness(0.5) invert(1)}

.SideStoryPanel{display:flex;align-items:center;justify-content:flex-end;pointer-events:none;transition:0.2s ease-out opacity}.SideStoryPanel .SideStoryPanel--panel{box-sizing:border-box;width:50%;padding-right:0;padding-right:var(--page-margin, 0);opacity:0;transition:0.2s ease-out opacity}.SideStoryPanel .SideStoryPanel--panel .SideStoryPanel--title{display:block;color:white;font-size:3rem;margin:0;margin-bottom:3rem}.SideStoryPanel .SideStoryPanel--panel .SideStoryPanel--description{margin-right:10rem;color:white;font-size:1.5rem;max-width:100rem;text-align:justify}.SideStoryPanel.visible .SideStoryPanel--panel{opacity:1}

.iframe-container{position:relative;width:100%;padding-bottom:56.25%;height:0;-webkit-clip-path:polygon(0% 15%, 100% 15%, 100% 85%, 0% 85%);clip-path:polygon(0% 15%, 100% 15%, 100% 85%, 0% 85%)}.iframe-container iframe{position:absolute;top:0;left:0;width:100%;height:100%}.MiddlePanel{display:flex;align-items:center;justify-content:center;pointer-events:none;transition:0.2s ease-out opacity}.MiddlePanel--panel{box-sizing:border-box;width:80%;opacity:0;transition:0.2s ease-out opacity;display:flex;flex-direction:column;align-items:center;max-width:100rem}.MiddlePanel--title{display:block;color:white;font-size:3.2rem;margin:0;margin-bottom:3rem;text-align:center}.MiddlePanel--description{color:white;font-size:2.0rem;max-width:80rem;text-align:justify}.MiddlePanel.visible .MiddlePanel--panel{opacity:1}

.Stat{width:23rem;padding:2rem 0.5rem;border-radius:50%;margin:1rem;text-align:center}.Stat__icon{width:100%;font-size:4rem;margin-bottom:1rem}.Stat__number{width:100%;text-align:center;font-size:3rem;font-weight:600}.Stat__name{width:100%;text-align:center;font-size:2rem;margin-top:0.5rem}

.StatContainer{width:100%;display:flex;justify-content:center;flex-wrap:wrap;margin:4rem 0}.StatContainer__pair{width:66rem;display:flex;justify-content:space-around}

.TeamCard{width:100%;max-width:70rem;background-color:white;margin-top:7rem;transition-duration:0.5s;box-shadow:2px 3px 1rem rgba(0,0,0,0.63);pointer-events:auto;display:flex;min-height:16rem;position:relative}.TeamCard *{box-sizing:border-box}.TeamCard:hover{-webkit-transform:scale(1.05) translateY(-1rem);transform:scale(1.05) translateY(-1rem);box-shadow:0px 5px 2rem rgba(0,0,0,0.753)}.TeamCard__img-block{width:60%;height:100%;position:relative;background:linear-gradient(135deg, #21d4fd, #b721ff);background:none !important}.TeamCard__img{position:absolute;width:120%;height:120%;object-fit:cover;object-position:40% 0%;top:-20%;right:0}.TeamCard__img-background{width:100%;height:100%;object-fit:scale-down}.TeamCard__heading{color:black;display:block;width:100%;font-size:2rem;text-transform:uppercase;text-align:center;z-index:50;padding:1rem;margin:0rem}.TeamCard__content{position:relative;background-color:#ffffff;background:linear-gradient(95deg, rgba(255,255,255,0) 5%, #fff 6%, #fff 94%, rgba(255,255,255,0) 95%);width:100%;height:100%;padding:1rem 4rem;color:#000000;display:flex;justify-content:center;align-content:center;flex-wrap:wrap;z-index:10}.TeamCard__description{display:block;margin:0rem;width:100%;font-size:1.2rem;opacity:0.7;text-align:center}.TeamCard__stats-container{width:50%;height:100%;background:linear-gradient(135deg, #21d4fd, #b721ff);display:flex;flex-direction:column;background:none !important}.TeamCard__stat{width:100%;height:100%;display:flex;justify-content:center;align-content:center;flex-wrap:wrap;padding:2px;line-height:2rem;color:white}.TeamCard__stat:not(:first-child){border-top:1px solid #ffffff55}.TeamCard__value{width:100%;font-size:1.5rem;text-align:center}.TeamCard__value--big{font-size:2rem}.TeamCard__name{width:100%;font-size:1rem;opacity:0.7;text-align:center}

.TeamSection{position:relative;left:50%;width:50%;height:100%;max-width:100%;color:white}.TeamSection__container{width:100%;max-width:100rem;height:100%;display:flex;justify-content:space-around;align-items:center;flex-wrap:wrap}.TeamSection__pair{width:100%;max-width:100%;display:flex;justify-content:space-around;flex-wrap:wrap}

.DefaultCard{position:fixed;z-index:9000;border-radius:20px;min-height:-webkit-fit-content;min-height:-moz-fit-content;min-height:fit-content;pointer-events:all;max-width:100%;width:500px;top:50vh;left:50vw;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);transition-duration:0.6s;transition-timing-function:ease-in-out;color:white}.DefaultCard--bigger-header .DefaultCard__heading{margin:0}.DefaultCard--bigger-header .DefaultCard__content{padding-bottom:14%;height:100%;width:100%;background:linear-gradient(-3deg, rgba(255,255,255,0) 14%, #fff 14.5%, #fff 75%, rgba(255,255,255,0) 75.5%);padding:20px;padding-top:165px;padding-bottom:125px}.DefaultCard--disable-left{left:-100vw !important}.DefaultCard--disable-right{left:200vw !important}.DefaultCard *{box-sizing:border-box}.DefaultCard__header{position:absolute;top:0;width:100%;padding:20px}.DefaultCard__heading{margin:0;font-size:2.5rem}.DefaultCard__subheading{margin:0;font-size:2rem}.DefaultCard__content{padding-bottom:14%;height:100%;width:100%;background:linear-gradient(-3deg, rgba(255,255,255,0) 14%, #fff 14.5%, #fff 80%, rgba(255,255,255,0) 80.5%);padding:20px;padding-top:145px;padding-bottom:125px}.DefaultCard__description{color:black;font-size:1.5rem;display:flex;flex-direction:column;align-items:center}.DefaultCard__description img{margin:15px auto;max-height:300px}.DefaultCard__footer{position:absolute;bottom:0;width:100%;text-align:right;padding:1.5rem;font-size:2rem}

.ScrollView{position:relative;pointer-events:none;overflow:hidden;display:flex;flex-direction:column}.ScrollView .ScrollView__Rod{position:absolute;top:0;bottom:0}.ScrollView .ScrollView__Rod.ScrollView__Rod--left{left:0;left:var(--page-margin, 0)}.ScrollView .ScrollView__Rod.ScrollView__Rod--right{right:0;right:var(--page-margin, 0)}.ScrollView .ScrollView__Rod__vertical{background-color:white;width:1px;height:100%}.ScrollView .ScrollView__Rod__cross{position:absolute;top:7px;width:15px;margin-left:-7px;height:1px;background-color:white}.ScrollView .ScrollView__handle-container{position:absolute;right:0;right:var(--page-margin, 0);margin-top:7px;pointer-events:initial}.ScrollView .ScrollView__handle{position:absolute;left:-8px;width:15px;height:100px;background-color:white;pointer-events:initial}.ScrollView .ScrollView__handle:hover{cursor:pointer}.ScrollView .ScrollView__contents{overflow-y:scroll;pointer-events:initial;-ms-overflow-style:none;scrollbar-width:none}.ScrollView .ScrollView__contents::-webkit-scrollbar{display:none}

