.Banner {
    position: relative;
    width: 100%;
    height: 100vh;

    &__overlay {
        width: 100%;
        height: 100vh;
        display: flex;
        align-items: center;

        &-text {
            position: absolute;
            box-sizing: border-box;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            color: #ffffff;
            width: 200rem;
            max-width: 80%;
            padding: 3rem;
            font-size: 4.5rem;
            line-height: 1.5;
            font-weight: 700;
            text-align: left;
        }
    }

    &__action {
        width: 100%;
        height: 20vh;
        position: absolute;
        left: 0;
        bottom: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-size: 2rem;
        font-weight: bold;
        color: white;
        overflow: hidden;        
        text-transform: uppercase;
        opacity: .6;
        // &-button {
        //     cursor: pointer;
        //     transform: translateY(0px) scaleX(1);
        //     transition-duration: .3s;
        //     animation: jello-vertical 5s both;
        //     animation-iteration-count: infinite;
        //     animation-delay: 2s;
        //     font-size: 3rem;

        //     &:hover {
        //         transform: translateY(10px) scale(1.2);
        //     }
        // }
    }

    &__social-media{
        position: absolute;
        bottom: 10rem;
        z-index: 100;
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;

        &-pair{
            position: relative;
            width: 75rem;
            height: 10rem;
            display: flex;
            justify-content: space-around;
        }

        &-link{
            width: 20rem;
            height: 10rem;
            position: relative;
            transform: scale(1);
            transition-duration: .5s;
            &:hover{
                transform: scale(1.2);
            }
        }

        &-img{
            position: absolute;
            width: 100%;
            top:50%;
            left: 50%;
            transform: translate(-50%, -50%);
            filter: blur(0px) grayscale(1) brightness(0.5) invert(1);
        }

        
    }
}