.Header {
    z-index: 1;
    position: fixed;
    top: 40px;
    display: flex;
    box-sizing: border-box;
    width: 100%;
    justify-content: flex-start;
    align-items: center;
    margin: 0 var(--page-margin, 0px);

    .actions {
        margin: 3rem;
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding: 0 2rem;
        border-radius: 0.1rem;

        a {
            position: relative;
            padding: 1rem 3rem;
            // margin: 0 0.7rem;
            color: white;
            border: 0;
            // font-family: 'Noto Sans', sans-serif;
            font-size: 1rem;
            text-decoration: none;
            transition: 0.2s ease-out color, 0.2s ease-out background-color;

            &::after {
                content: "";
                position: absolute;
                bottom: 25%;
                left: 50%;
                transform: translateX(-50%) translateY(2px) scaleX(0);
                width: calc(100% - 6rem);
                height: 1px;
                background: white;
                transition: 0.1s ease-in-out background-color, 0.1s ease-out transform;
            }

            &.active {
                background-color: white;
                color: black;
            }

            &:not(.active):hover::after {
                transform: translateX(-50%) translateY(2px) scaleX(1);
            }
        }
    }
}
