.iframe-container{
    position: relative;
    width: 100%;
    padding-bottom: 56.25%; 
    height: 0;
    clip-path: polygon(0% 15%, 100% 15%, 100% 85%, 0% 85%);
    
}
.iframe-container iframe{
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
}
.MiddlePanel {
    display: flex;
    align-items: center;
    justify-content: center;

    pointer-events: none;
    transition: 0.2s ease-out opacity;
    
    &--panel {
        box-sizing: border-box;
        width: 80%;
        // padding-right: var(--page-margin, 0);
        opacity: 0;
        transition: 0.2s ease-out opacity;
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: 100rem;
    }
    &--title {
        display: block;
        color: white;
        font-size: 3.2rem;
        margin: 0;
        margin-bottom: 3rem;
        text-align: center;
    }

    &--description {
        // margin-right: 10rem;
        color: white;
        font-size: 2.0rem;
        max-width: 80rem;
        text-align: justify;
    }
    &.visible {
        .MiddlePanel--panel {
            opacity: 1;
        }
    }
}