.DefaultCard {
    position: fixed;
    // margin-top: 40px;
    z-index: 9000;
    border-radius: 20px;
    min-height: fit-content;
    // position: relative;
    pointer-events: all;
    max-width: 100%;
    width: 500px;
    top: 50vh;
    left: 50vw;
    transform: translate(-50%, -50%);
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    color: white;

    &--bigger-header {
        .DefaultCard__heading {
            margin: 0;
            // font-size: 3.2rem;
        }
        .DefaultCard__content {
            $size-top: 25%;
            $size-bottom: 14%;

            padding-bottom: $size-bottom;
            height: 100%;
            width: 100%;
            background: linear-gradient(
                -3deg,
                #ffffff00 $size-bottom,
                #ffffff $size-bottom + 0.5%,
                #ffffff 100%-$size-top,
                #ffffff00 100%-$size-top + 0.5%
            );
            padding: 20px;
            padding-top: 165px;
            padding-bottom: 125px;
        }
    }

    &--disable-left {
        left: -100vw !important;
    }
    &--disable-right {
        left: 200vw !important;
    }

    * {
        box-sizing: border-box;
    }
    &__header {
        position: absolute;
        top: 0;
        width: 100%;
        padding: 20px;
    }
    &__heading {
        margin: 0;
        font-size: 2.5rem;
    }
    &__subheading {
        margin: 0;
        font-size: 2rem;
    }
    &__content {
        $size-top: 20%;
        $size-bottom: 14%;

        padding-bottom: $size-bottom;
        height: 100%;
        width: 100%;
        background: linear-gradient(
            -3deg,
            #ffffff00 $size-bottom,
            #ffffff $size-bottom + 0.5%,
            #ffffff 100%-$size-top,
            #ffffff00 100%-$size-top + 0.5%
        );
        
        padding: 20px;
        padding-top: 145px;
        padding-bottom: 125px;
    }

    &__description {
        color: black;
        font-size: 1.5rem;
        display: flex;
        flex-direction: column;
        align-items: center;

        img {
            margin: 15px auto;
            max-height: 300px;
        }
    }
    
    &__footer {
        position: absolute;
        bottom: 0;
        width: 100%;
        text-align: right;
        padding: 1.5rem;
        font-size: 2rem;
    }
}
