.TeamSection {
    position: relative;
    left: 50%;
    width: 50%;
    height: 100%;
    max-width: 100%;
    color: white;

    &__container {
        width: 100%;
        max-width: 100rem;
        height: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-wrap: wrap;
    }

    &__pair {
        width: 100%;
        max-width: 100%;
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
    }
}
